"use client";
import { useCart } from "@/context/cartContext";
import CartItem from "../cartItem/cartitem";
import Emptycart from "../emptycartmain/emptycart";
import { TiTimes } from "react-icons/ti";
function CartSider({ isOpen, isClosed }) {
  const [items, totalPrice, totalItems, clearCart] = useCart();
  return (
    <>
      {isOpen && <div className="cart-overlay" onClick={isClosed}></div>}

      <div className={`cart-sider ${isOpen ? "open" : ""}`}>
        {/* header */}

        <div className="cartSider-header">
          <h3>
            shopping Cart <Span>({totalItems})</Span>
          </h3>
          <button className="close-button" onClick={isClosed}>
            <TiTimes />
          </button>
        </div>

        {/* content */}
        <div className="cart-content">
          {items.length === 0 ? (
            <Emptycart onClose={isClosed} />
          ) : (
            /*items list */
            <div className="itemList">
              {items.map((item) => (
                <CartItem
                  key={`${item.id}- ${item.variant || "default"}`}
                  item={item}
                />
              ))}
            </div>
          )}
        </div>

        {/* footer*/}

        <div className=" cart-footer">
          <button className="clear-cart-btn" onClick={clearCart}>
            Clear Cart
          </button>

          <div className="cart-total">
            <p>
              <span>Total:</span> ${totalPrice.toFixed(2)}
            </p>
          </div>

          <button type="button" className="cart-checkout">
            Proceed to Checkout
          </button>
        </div>
      </div>
    </>
  );
}
export default CartSider;
